<template>
  <v-app-bar flat :class="getColor">
    <!-- TODO: hijacking this slot for drag handle cuz of its location in the card -->
    <v-app-bar-nav-icon v-if="isEditing" color="white" class="handle">
      <v-icon>mdi-drag-horizontal-variant</v-icon>
    </v-app-bar-nav-icon>

    <v-spacer></v-spacer>

    <v-btn
      v-if="isEditing || isMultiSelecting"
      color="white"
      icon
      @click.stop="$emit('toggle-selected')"
    >
      <v-icon>{{ deleteIcon }}</v-icon>
    </v-btn>
    <!-- TODO: special case - if this item is the avatar, you cant mark it for deletion - maybe a modal telling user to mark another image as avatar that isn't selected for deletion -->
    <v-btn
      v-if="isAvatar || isEditing"
      color="white"
      icon
      @click.stop="toggleCheck()"
    >
      <v-icon>{{ avatarIcon }}</v-icon>
    </v-btn>
  </v-app-bar>
</template>

<script>
// https://vuetifyjs.com/en/components/cards/#content-wrapping

// TODO: rules for showing 'select for delete':
//  if the card isAvatar => dont show it, if the card is selected => dont show avatar button
export default {
  data: () => ({}),
  props: {
    isEditing: {
      type: Boolean,
    },
    isSelected: Boolean,
    isAvatar: {
      type: Boolean,
    },
    isMultiSelecting: Boolean,
  },
  computed: {
    avatarIcon() {
      if (this.isAvatar) {
        return "mdi-star";
      }
      return "mdi-star-outline";
    },
    deleteIcon() {
      if (this.isMultiSelecting) {
        return this.isSelected
          ? "mdi-check-circle"
          : "mdi-checkbox-blank-circle-outline";
      }
      return this.isSelected
        ? "mdi-close-circle"
        : "mdi-checkbox-blank-circle-outline";
    },
    getColor() {
      return this.isEditing || this.isAvatar ? "gradient" : "transparent";
    },
  },
  methods: {
    toggleCheck() {
      console.log(`ACH.toggleAvatar`);
      if (this.isEditing) {
        this.$emit("toggle-avatar");
      }
    },
  },
};
</script>
<style scoped>
.gradient {
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0),
    rgb(0, 0, 0)
  ) !important;
}
.transparent {
  background: rgba(0, 0, 0, 0);
}
</style>