<template>
  <v-container>
    <DialogStateful
      :dialog="showSlideshow"
      @close-dialog="showSlideshow = false"
      :title="`View Asset`"
      :show-actions="editAsset"
    >
      <template #toggles>
        <v-btn icon @click.stop="editAsset = true" v-if="!editAsset">
          <v-icon>mdi-pencil-outline</v-icon>
        </v-btn>
        <v-simple-checkbox
          v-model="showAssetInfo"
          off-icon="mdi-information-outline"
          on-icon="mdi-information"
        >
        </v-simple-checkbox>
      </template>
      <template #content>
        <v-container class="pa-0 pt-5">
          <!-- TODO: this should be a flex container with one box visible and swipeable -->
          <!-- <v-img :src="getAssetSrc()" class="pt-5"></v-img>
          <div v-if="showAssetInfo">date taken, description</div> -->
          <v-carousel v-model="slideshowIndex" hide-delimiters>
            <v-carousel-item v-for="(item, i) in carouselList()" :key="i">
              <v-card>
                <v-img :src="item.src">
                  <!-- <template v-slot:placeholder>
                  <v-row
                    class="fill-height ma-0"
                    align="center"
                    justify="center"
                  >
                    <v-progress-circular
                      indeterminate
                      color="grey lighten-5"
                    ></v-progress-circular>
                  </v-row>
                </template> -->
                </v-img>
                <v-card-text v-if="showAssetInfo" class="pa-0">
                  date taken, description (editable)
                </v-card-text>
              </v-card>
            </v-carousel-item>
          </v-carousel>
        </v-container>
      </template>
      <template #actions>
        <v-btn text @click.stop="editAsset = false"> cancel </v-btn>
        <v-btn text @click.stop="editAsset = false"> save </v-btn>
      </template>
    </DialogStateful>
    <DialogContentScrollable
      :dialog="showDialog"
      @save="addImages"
      @close-dialog="showDialog = false"
      :title="`Add Assets`"
    >
      <template #toggles>
        <v-btn icon @click.stop="selectAll">
          <v-icon>mdi-plus-box-multiple-outline</v-icon>
        </v-btn>
        <v-btn icon @click.stop="deselectAll">
          <v-icon>mdi-image-filter-none</v-icon>
        </v-btn>
      </template>
      <template #content>
        <v-container class="pa-0 pt-5">
          <v-select
            v-model="selectedAlbum"
            :items="getAlbums"
            item-text="title"
            item-value="id"
            label="Select"
            return-object
            single-line
            solo
            class="row"
          ></v-select>
          <v-row class="mt-0">
            <v-col
              v-for="asset in albumImages()"
              :key="asset.id"
              class="d-flex child-flex"
              :cols="contentCols"
            >
              <ItemCard
                class="item-card"
                :is-tile="true"
                :show-metadata="showMetadata"
                :image-src="asset.src"
                @select-item="selectImage(asset.id)"
              >
                <template v-slot:header>
                  <AssetCardHeader
                    :is-multi-selecting="true"
                    :is-selected="isImageSelected(asset.id)"
                    @toggle-selected="toggleImageSelected(asset.id)"
                  />
                </template>
                <template v-slot:metadata>
                  <div>
                    {{ asset.description }}
                  </div>
                </template>
              </ItemCard>
            </v-col>
          </v-row>
        </v-container>
      </template>
    </DialogContentScrollable>
    <!-- <v-row> -->
    <v-form class="row">
      <v-expansion-panels accordion>
        <v-expansion-panel v-if="isEditing">
          <v-expansion-panel-header>
            Name
            <template v-slot:actions>
              <v-icon> mdi-pencil </v-icon>
            </template>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <v-text-field
              v-model="item.name"
              label="Item Name"
              required
            ></v-text-field>
          </v-expansion-panel-content>
        </v-expansion-panel>
        <v-expansion-panel>
          <v-expansion-panel-header>
            Attributes
            <template v-slot:actions v-if="isEditing">
              <v-icon> mdi-pencil </v-icon>
            </template>
          </v-expansion-panel-header>
          <v-expansion-panel-content v-if="!isEditing">
            <v-chip v-for="attribute in item.attributes" :key="attribute.id">
              {{ attribute.title }}
            </v-chip>
            <span v-if="!item.attributes.length">NONE</span>
          </v-expansion-panel-content>
          <v-expansion-panel-content v-else>
            <AttributeSearch
              :attributes="sortedAttributes"
              v-model="item.attributes"
            />
          </v-expansion-panel-content>
        </v-expansion-panel>
        <v-expansion-panel>
          <v-expansion-panel-header>
            Description
            <template v-slot:actions v-if="isEditing">
              <v-icon> mdi-pencil </v-icon>
            </template>
          </v-expansion-panel-header>
          <v-expansion-panel-content v-if="!isEditing">
            {{ item.description }}
          </v-expansion-panel-content>
          <v-expansion-panel-content v-else>
            <v-text-field
              v-model="item.description"
              label="Item Name"
              required
            ></v-text-field>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </v-form>
    <!-- </v-row> -->
    <v-row v-if="!sortableList.length" justify="center" align="center">
      NONE
    </v-row>

    <draggable
      v-else
      :list="sortableList"
      :disabled="!isEditing"
      class="row"
      justify="center"
      align="center"
      :group="sortableList"
      ghost-class="ghost"
      :move="checkMove"
      @start="dragging = true"
      @end="dragging = false"
      @choose="onChoose"
      @unchoose="onUnchoose"
      :options="{ handle: '.handle' }"
    >
      <v-col
        v-for="asset in sortableList"
        :key="asset.id"
        class="d-flex child-flex"
        :cols="contentCols"
      >
        <!-- TODO: if/else in v-for is workaround for draggable with fixed unique element -->
        <v-btn
          v-if="shouldShowAddTile(asset.fixed)"
          width="100%"
          height="100%"
          class="fill-height"
          @click.stop="openAddAssetDialog"
          min-height="150"
        >
          <v-icon left> mdi-plus </v-icon>{{ asset.title }}
        </v-btn>

        <ItemCard
          v-else
          :is-tile="true"
          :show-metadata="showMetadata"
          :image-src="asset.src"
          @select-item="selectAsset(asset.id)"
        >
          <template v-slot:header>
            <AssetCardHeader
              :is-editing="isEditing"
              :is-avatar="isAvatar(asset.id)"
              :is-selected="isSelected(asset.id)"
              @toggle-avatar="toggleAvatar(asset.id)"
              @toggle-selected="toggleSelected(asset.id)"
            />
          </template>
          <template v-slot:metadata>
            <div>
              {{ asset.description }}
            </div>
          </template>
        </ItemCard>
      </v-col>
    </draggable>
  </v-container>
</template>
<script>
import { mapState, mapGetters, mapActions } from "vuex";
import draggable from "vuedraggable";
import ItemCard from "@/components/ItemCard.vue";
import AssetCardHeader from "@/components/AssetCardHeader.vue";
import AttributeSearch from "@/components/AttributeSearch";
import DialogContentScrollable from "@/components/DialogContentScrollable";
import DialogStateful from "@/components/DialogStateful";

const newItem = {
  id: null,
  name: null,
  description: null,
  attributes: [],
  assets: [],
  avatar: null,
};
export default {
  components: {
    draggable,
    ItemCard,
    AssetCardHeader,
    AttributeSearch,
    DialogContentScrollable,
    DialogStateful,
  },
  data: () => ({
    showDialog: false,
    showSlideshow: false,
    showAssetInfo: false,
    editAsset: false,
    // TODO: this probably should be an index
    slideshowIndex: 0,
    slideshowAsset: null,
    newItem: { ...newItem },
    item: { ...newItem },
    // TODO: rename - these are assets to remove
    selectedAssetIds: [],
    // TODO: rename - these are images to add
    selectedImageIds: [],
    sortableList: [],
    addElement: { fixed: true, id: "-1", title: "add" },
    selectedAlbum: null,
  }),
  mounted() {
    // TODO: better way of managing these flags - might defer to mvp
    if (!this.isAddingItem()) {
      this.setEditingFlag(false);
      this.initItem();
    } else {
      this.setEditingFlag(true);
    }
    // https://scrimba.com/scrim/cqKRgEC9?pl=pnyzgAP to fix these kinds of things
    // basically, i want the item to be editable, not copy pieces of it to use as v-model to children, then stitch them back together before save...
    this.sortableList = this.isEditing
      ? [this.addElement, ...this.item.assets]
      : [...this.item.assets];
  },
  created() {
    console.log(`Item:created`);
  },
  computed: {
    ...mapGetters([
      "getPrefs",
      "sortedAttributes",
      "getAttributesByTitle",
      "getNewestItemId",
      "getAlbums",
      "getAlbum",
    ]),
    ...mapState(["isEditing", "isSaving", "isDeleting"]),
    showMetadata() {
      return this.getPrefs.showMetadata;
    },
    contentCols() {
      // console.log(`contentCols`);
      // TODO: fix layout of card header app bar
      // return this.isEditing ? 4 : 6;
      return 6;
    },
  },
  props: {
    // slug: { type: String, required: true },
    slug: String,
  },
  methods: {
    ...mapActions([
      "setEditingFlag",
      "setSavingFlag",
      "addItem",
      "updateItem",
      "addAttributes",
      "deleteItem",
    ]),
    carouselList() {
      // sortedList or albumList
      if (this.showDialog) {
        return this.albumImages();
      }
      return this.sortableList;
    },
    selectAll() {
      // this.sortableList.splice()
      // this.selectedImageIds.splice()
      const albumImageIds = this.albumImages().map((img) => img.id);
      this.selectedImageIds.splice(0, null, ...albumImageIds);
    },
    deselectAll() {
      this.selectedImageIds.splice(0);
    },
    addImages() {
      console.log(`Item.addImages - selectedImages: ${this.selectedImageIds}`);
      if (!this.selectedImageIds.length) {
        console.log(
          `TODO: this could be a case where user has removed images previously added`
        );
      }
      const newSelectedImageAssets = this.selectedImageIds
        .filter((i) => {
          return !this.sortableList.find((sli) => sli.id === i);
        })
        .map((id) => {
          return {
            id,
            src: this.albumImages().find((i) => i.id === id)?.src,
          };
        });
      // TODO: need to remove previously added and newly removed images from sortableList

      this.sortableList.splice(
        1, // still in editing state, so dont overwrite add button
        0, // only adding
        ...newSelectedImageAssets
      );
      this.showDialog = false;
    },
    albumImages() {
      // console.log(`Item.albumImages`);
      const albumId = this.selectedAlbum?.id;
      if (albumId) return this.getAlbum(albumId).images;
      return [];
    },
    isAddingItem() {
      return !this.$route.params.slug || this.$route.params.slug === "add";
    },
    shouldShowAddTile(fixedFlag) {
      return this.isEditing && fixedFlag;
    },
    isSelected(id) {
      return this.selectedAssetIds.includes(id);
    },
    toggleSelected(id) {
      const index = this.selectedAssetIds.indexOf(id);
      if (index >= 0) {
        this.selectedAssetIds.splice(index, 1);
      } else {
        this.selectedAssetIds.push(id);
      }
      console.log(`Item.toggleSelected, ids: ${this.selectedAssetIds}`);
    },
    isAvatar(id) {
      return this.item.avatar?.id === id || false;
    },
    toggleAvatar(id) {
      console.log(`Item.toggleAvatar - id: ${id}`);
      if (this.item.avatar?.id === id) {
        this.item.avatar = null;
      } else {
        this.item.avatar = this.sortableList.find((a) => a.id === id);
      }
    },
    selectAsset(id) {
      console.log(`Item.selectAsset: ${id} - this should open a slideshow`);
      this.slideshowIndex = this.sortableList.findIndex((i) => i.id === id);
      this.showSlideshow = true;
    },
    selectImage(id) {
      console.log(
        `Item.selectAlbumAsset: ${id} - should this open image viewer dialog with no edits?`
      );
      this.slideshowIndex = this.albumImages().findIndex((i) => i.id === id);
      this.showSlideshow = true;
    },
    isImageSelected(id) {
      return this.selectedImageIds.includes(id);
    },
    toggleImageSelected(id) {
      const index = this.selectedImageIds.indexOf(id);
      if (index >= 0) {
        this.selectedImageIds.splice(index, 1);
      } else {
        this.selectedImageIds.push(id);
      }
      console.log(`Item.toggleSelected, ids: ${this.selectedImageIds}`);
    },
    openAddAssetDialog() {
      console.log(`Item.openAddAssetDialog`);
      this.showDialog = true;
    },
    onChoose() {
      console.log(`Item.choose`);
    },
    onUnchoose() {
      console.log(`Item.unChoose`);
    },
    checkMove(e) {
      console.log(`Item.checkMove`);
      return this.isDraggable(e.draggedContext);
    },
    isDraggable(context) {
      const { index, futureIndex } = context;
      return !(
        this.sortableList[index].fixed || this.sortableList[futureIndex].fixed
      );
    },
    getNewAttrs() {
      // TODO: the new attrs aren't in the sorted list; they are in the edited item.attrs with a fake id.
      // filter new attrs and save them, then save the item
    },
    getAssetSrc() {
      if (this.slideshowAsset) {
        return this.slideshowAsset.src;
      }
      return "";
    },
    initItem() {
      this.clearEdits();
    },
    clearEdits() {
      if (this.isAddingItem()) {
        this.item = { ...this.newItem };
      } else {
        this.item = {
          ...this.$store.getters.getPopulatedItem(this.$route.params.slug),
        };
      }
      this.slideshowAsset = this.item.assets[0] || null;
    },
  }, // methods end
  watch: {
    slideshowIndex(val) {
      this.slideshowAsset = this.sortableList[val || 0];
    },
    selectedAlbum(val) {
      console.log(`Item:watch:selectedAlbum: ${val}`);
    },
    $route() {},
    // deep watch
    item: {
      handler(val) {
        console.log(`watch:item changed - avatar: ${val.avatar}`);
        // do stuff
      },
      deep: true,
    },
    "item.attributes"(newVal) {
      console.log(`Item:watch:item.attributes: ${newVal}`);
    },
    isEditing(val) {
      console.log(`Item:watch:isEditing changed: ${val}`);
      // race condition. when saving, the flags are set - are they always in the defined order?
      if (!val && !this.isSaving) this.clearEdits();

      // TODO: this is redundant - created does this too
      const btnInList = !!this.sortableList[0]?.fixed || false;
      const shouldEditList = btnInList === !val; // xor
      // console.log(`isEditing btnInList: ${btnInList}`);
      // console.log(`isEditing shouldEditList: ${shouldEditList}`);

      if (shouldEditList) {
        if (btnInList) {
          this.sortableList.shift();
        } else {
          this.sortableList.unshift(this.addElement);
        }
      }
    },
    isSaving(val) {
      console.log(`Item:watch:isSaving changed: ${val}`);
      if (val) {
        // TODO: temp implicitly adding new attributes
        const newAttrs = this.item.attributes.filter((a) => {
          console.log(`is this a new attr? ${a.id}`);
          return a.id <= 0;
        });
        let savedAttrs = [];
        if (newAttrs.length) {
          this.addAttributes(newAttrs);
          savedAttrs = this.getAttributesByTitle(newAttrs.map((a) => a.title));
        }
        this.item.attributes = this.item.attributes.map((a) => {
          console.log(
            `is this where this.item.attributes gets set to an array of ids?`
          );
          const savedAttr = savedAttrs.find((saved) => saved.title === a.title);
          if (savedAttr) {
            return {
              ...a,
              id: savedAttr.id,
            };
          }
          return a;
        });
        this.item.assets = [
          ...this.sortableList.filter((i) => {
            // TODO: rename this - this is a list of ids to remove
            return !this.selectedAssetIds.includes(i.id) || i.fixed;
          }),
        ];
        if (this.isAddingItem()) {
          this.addItem(this.item);
        } else {
          this.updateItem(this.item);
          // TODO: refactor out rehydration after add/save - maybe composition api might help?
          this.sortableList = [...this.item.assets];
        }
        // this is being set in the store...
        // this.setSavingFlag(false);
        if (this.isAddingItem()) {
          this.$router.replace({
            name: "Item",
            params: { slug: this.getNewestItemId },
          });
        }
      }
    },
    isDeleting(val) {
      console.log(`Item:watch:isDeleting changed: ${val}`);
      this.deleteItem(this.item);
      this.$router.replace("/catalog");
    },
  },
};
</script>
<style>
.item-card:before {
  content: "";
  display: block;
  padding-top: 100%;
}
</style>