<template>
  <!-- <v-combobox
    :value="value"
    @input="$emit('input', $event)"
    :items="attributes"
    :search-input.sync="searchInput"
    @change="searchInput = ''"
    item-text="title"
    item-value="id"
    return-object
    :filter="filter"
    :hide-no-data="!searchInput"
    hide-selected
    label="Search/Create Attribute"
    multiple
    small-chips
    solo
  > -->
  <v-combobox
    v-model="inputVal"
    :items="attributes"
    :search-input.sync="searchInput"
    @change="searchInput = ''"
    item-text="title"
    item-value="id"
    return-object
    :filter="filter"
    :hide-no-data="!searchInput"
    hide-selected
    label="Search/Create Attribute"
    multiple
    small-chips
    solo
  >
    <template v-slot:no-data>
      <v-list-item>
        <span class="subheading">
          <!-- <v-btn @click="createAttribute(searchInput)"> Create </v-btn> -->
          Create
        </span>
        <!-- <v-chip :color="`${colors[nonce - 1]} lighten-3`" label small> -->
        <v-chip :color="`lighten-3`" label small>
          {{ searchInput }}
        </v-chip>
      </v-list-item>
    </template>
    <template v-slot:selection="{ attrs, item, parent, selected }">
      <v-chip v-bind="attrs" :input-value="selected" label small>
        <span class="pr-2">
          {{ item.title }}
        </span>
        <v-icon small @click="parent.selectItem(item)"> $delete </v-icon>
      </v-chip>
    </template>
  </v-combobox>
</template>

<script>
// https://vuetifyjs.com/en/components/combobox/#advanced-custom-options
export default {
  data: () => ({
    activator: null,
    attach: null,
    editing: null,
    editingIndex: -1,
    fakeId: "0",
    x: 0,
    searchInput: "",
    y: 0,
    // selectedAttrs: [],
  }),

  props: {
    attributes: {
      type: Array,
    },
    // v-model = value
    value: {
      type: Array,
    },
  },
  computed: {
    inputVal: {
      get() {
        return this.value;
      },
      set(val) {
        // console.log(`inputVal:set ${JSON.stringify(val)}`);
        // console.log(`can i change val here?`);
        val = val.map((v) => {
          if (typeof v === "string") {
            // console.log(`${v} is a string`);
            const id = this.fakeId--;
            return {
              title: v,
              id: id.toString(),
            };
          }
          return v;
        });
        this.$emit("input", val);
      },
    },
  },

  watch: {
    // value(val, prev) {
    //   console.log(`value: { val: ${val}, prev: ${prev} }`);
    // },
    // inputVal(val, prev) {
    //   console.log(`inputVal: { val: ${val}, prev: ${prev} }`);
    // },
  },

  methods: {
    createAttribute(title) {
      this.inputVal.set(title);
    },
    edit(index, item) {
      console.log(`AttributeSearch:edit { idx: ${index}, item: ${item}}`);
      if (!this.editing) {
        this.editing = item;
        this.editingIndex = index;
      } else {
        this.editing = null;
        this.editingIndex = -1;
      }
    },
    filter(item, queryText, itemText) {
      if (item.header) return false;

      const hasValue = (val) => (val != null ? val : "");

      const text = hasValue(itemText);
      const query = hasValue(queryText);

      return (
        text.toString().toLowerCase().indexOf(query.toString().toLowerCase()) >
        -1
      );
    },
  },
};
</script>